* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 10px;
}

body {
  font-family: Roboto, sans-serif;
  font-weight: 400;
  font-size: 1.6rem;
}

h2 {
  font-size: 4.8rem;
  font-weight: 400;
  line-height: 58px;
}

a {
  text-decoration: none;
}

.subtitle {
  font-size: 24px;
  font-weight: 400;
  line-height: 29px;
}

.wrapper {
  position: relative;
  width: 1160px;
  height: 100%;
  margin: 0 auto;
  overflow: hidden;
}

.header {
  height: 80px;
  background: #000;
}
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header a {
  color: #fff;
}
.header__copyright {
  flex-grow: 1;
  text-align: right;
}
.header__menu {
  margin-left: 64px;
}

.logo {
  position: relative;
  width: 82px;
  height: 52px;
  border: 1px solid #fff;
  border-radius: 2px;
  text-align: center;
  font-size: 19px;
  padding-top: 4px;
}
.logo__icon {
  position: absolute;
  bottom: 6px;
  left: 9px;
  display: block;
  width: 15px;
  height: 21px;
  background-image: url("../../../assets/icons/bamboo.svg");
  background-repeat: no-repeat;
}

.menu__item {
  list-style: none;
  display: inline-block;
  margin-right: 54px;
}

.promo {
  height: 700px;
  background-image: url("../../../assets/images/giant_panda.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
.promo__text {
  position: relative;
  margin-left: auto;
  margin-top: 84px;
  width: 500px;
  font-size: 106px;
  font-weight: 700;
  line-height: 80px;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
}
.promo__text span {
  color: #FFEE2E;
}
.promo__figure {
  position: absolute;
  right: 108px;
  top: 12px;
  width: 465px;
  height: 465px;
  border-radius: 50%;
  background: linear-gradient(113.96deg, #F9804B 1.49%, #FE9013 101.44%);
}
.promo__button {
  margin-left: auto;
  margin-top: 62px;
  margin-right: 99px;
}

.button {
  display: block;
  width: 298px;
  height: 54px;
  background: linear-gradient(113.96deg, #F9804B 1.49%, #FE9013 101.44%);
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
  text-transform: uppercase;
  cursor: pointer;
  border: none;
}
.button:hover {
  background: #E47209;
}
.button:active {
  background: #4B9200;
}
.button_rounded {
  border-radius: 25px;
}

.backstage {
  height: 721px;
  background-image: url("../assets/images/flower_photo1.png");
  background-repeat: no-repeat;
  background-position: right 12px;
}
.backstage__data {
  margin-top: 140px;
  height: 403px;
  display: flex;
  justify-content: space-between;
  column-gap: 86px;
}
.backstage__text-block {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.backstage__text-block p {
  line-height: 22px;
}
.backstage__text-block p:last-child {
  margin-bottom: -2px;
}
.backstage__title {
  margin-bottom: 20px;
}
.backstage__subtitle {
  margin-bottom: 34px;
}